#leafletMap
{
    height: 500px;
}
.leaflet-control-container,
.leaflet-left,
.leaflet-timeline-controls
{
    width: 100%;
}
.leaflet-control-layers-expanded
{
    border: none;
    background: transparent;
}
output
{
    font-size: 4em;

    margin: 0;
    padding: 0;
}
.play,
.pause
{
    color: white;
    border: 2px solid black;
    background-color: #0096c7;
}
.play:hover,
.pause:hover
{
    color: white;
    background-color: #0096c7;
}