#dataviz
{
    width: 95%;
    height: auto;
    margin: auto;
}
#dataviz rect.bar
{
    opacity: 0.4;
    /* Data URI encoded from: stillcaptive.svg */
    /*background: #fff url(data:image/svg+xml;base64,) top repeat-x;
    background-size: contain;*/
}
#dataviz g.selected > rect.bar
{
    stroke: red;
    stroke-width: 2px;
}
#dataviz .axis text
{
    font: 10px sans-serif;
}
#dataviz .axis text.label
{
    font: 15px sans-serif;
}
#dataviz g.selected > text
{
    fill: red;
}
#dataviz .groups g > text:hover,
.map-legend li:hover
{
    cursor: pointer;
    text-decoration: underline;
}
#dataviz .axis line,
#dataviz .axis path
{
    fill: none;
    stroke: none;
    shape-rendering: crispEdges;
}
#dataviz .axis text
{
    fill: #666;
}
#dataviz .grid line
{
    fill: none;
    stroke: #eee;
    shape-rendering: crispEdges;
}
#dataviz .barGroup text
{
    font-size: .9em;
}
#dataviz .point
{
    stroke-width: 2px;
    stroke: white;
}