html,
body
{
    height: 100%;
}
header
{
    padding-top: 12px;

    border: none;
    background-color: #232a2c;
}
.no-svg header h1
{
    font-size: 6em;
    font-weight: 200;

    display: block;

    margin-bottom: 24px;

    letter-spacing: -.02em;

    color: #b6c0c6;
}
@media screen and (max-width: 460px)
{
    header h1
    {
        font-size: 5em;
        font-weight: 200;

        margin-bottom: 24px;

        letter-spacing: -.02em;

        color: #b6c0c6;
    }
}
header h1 small
{
    font-size: 50%;
    font-weight: 100;

    display: block;

    padding-top: 10px;
    padding-bottom: 15px;

    letter-spacing: .01em;

    color: #879498;
}
#loader
{
    margin-top: 24px;
}
#moreInfo .description
{
    font-size: .9em;
}
#moreInfo table > tbody > tr > td
{
    border: 0;
}
.my-legend .legend-scale ul
{
    margin: 0;
    margin-bottom: 0;
    padding: 0;

    list-style: none;
}
.my-legend .legend-scale ul li
{
    font-size: 90%;
    line-height: 24px;

    display: inline-block;

    width: 9%;
    min-width: 140px;
    margin: 1px;
    padding: 2px;
    padding-top: 12px;

    list-style: none;
}
@media screen and (max-width: 460px)
{
    .my-legend .legend-scale ul li
    {
        font-size: 80%;
        line-height: 24px;

        display: inline-block;

        width: 9%;
        min-width: 125px;
        margin: 1px;
        padding: 2px;
        padding-top: 12px;
    }
}
.legend
{
    margin-top: 12px;
}
.my-legend .legend-scale ul.legend-locations li:hover
{
    cursor: pointer;
}
.my-legend .legend-scale ul.legend-locations.selected li.selected span
{
    -webkit-box-shadow: 0 0 3px .3px #23292b;
       -moz-box-shadow: 0 0 3px .3px #23292b;
            box-shadow: 0 0 3px .3px #23292b;

         -o-box-shadow: 0 0 3px .3px #23292b;
    filter: progid: DXImageTransform.Microsoft.Shadow(color=#656565, Direction=NaN, Strength=3);
}
.my-legend .legend-scale ul.legend-locations.selected li:not(.selected) span
{
    background-color: #e8eef1 !important;
}
#legend ul.nocolor li span
{
    background-color: #dadada !important;
}
.my-legend ul.legend-labels li span
{
    display: block;
    float: left;

    width: 24px;
    height: 24px;
    margin-top: 1px;
    margin-right: 5px;
    margin-left: 0;

    border: 3px solid white;
    -webkit-border-radius: 3px;
       -moz-border-radius: 3px;
            border-radius: 12px;
}
.my-legend .legend-source
{
    font-size: 70%;

    clear: both;

    color: #999;
}
.my-legend a
{
    color: #777;
}
#legend ul
{
    margin-bottom: 24px;
}
.removeFilters:not(.selected)
{
    display: none;
}
.removeFilters
{
    cursor: pointer;
}
#countries-div
{
    margin-bottom: 15px;
}
.menu-block
{
    margin-top: 24px;
    margin-bottom: 12px;

    color: #747b7f;
}
.menu-block hr
{
    border: 1px solid black;
    border-top: 1px solid #d1dce3;
    border-bottom: 0;
}
.menu-block input
{
    margin-top: 10px;

    color: #91989c;
    border-color: #dde5ea;
    background-color: #e8eef1;
}
.right-inner-addon
{
    position: relative;
}
.right-inner-addon input
{
    padding-right: 30px;
}
.right-inner-addon span
{
    position: absolute;
    top: 7px;
    right: 0;

    padding: 10px 12px;

    pointer-events: none;
}
.menu-block input::-webkit-input-placeholder
{
    /* WebKit browsers */
    color: #91989c;
}
.menu-block input:-moz-placeholder
{
    opacity: 1;
    /* Mozilla Firefox 4 to 18 */
    color: #91989c;
}
.menu-block input::-moz-placeholder
{
    opacity: 1;
    /* Mozilla Firefox 19+ */
    color: #91989c;
}
.menu-block input:-ms-input-placeholder
{
    /* Internet Explorer 10+ */
    color: #91989c;
}
.menu-block select
{
    margin-top: 10px;

    color: #91989c;
    border-color: #dde5ea;
    background-color: #e8eef1;
}
.listHostages .label-warning {
    background-color: #c4bdb5;
}
.listHostages .label-primary {
    background-color: #87b9db;
}
.listHostages .label-default {
    background-color: #575e61;
}
.social
{
    padding-top: 36px;
}
.social img
{
    width: 48px;
}
@media screen and (max-width: 460px)
{
    .social img
    {
        width: 36px;
    }
}
nav.notjustified
{
    font-size: 10px;
    font-weight: 700;

    margin-top: 48px;
    padding-right: 0;
}
nav.notjustified li
{
    min-width: 20%;
    padding: 0;
}
.nav-tabs li a
{
    background-color: transparent !important;
}
.nav-tabs li.active a
{
    background-color: transparent !important;
}
.nav-tabs li a:hover
{
    border: none !important;
    background-color: transparent !important;
}
nav.notjustified a
{
    margin: 0;
    padding: 0;
    padding-bottom: 12px;

    border: 0;
}
nav.notjustified img
{
    width: 36px;
}
nav.nav-justified img
{
    width: 48px;
}
.nav-tabs.nav-justified > .active > a
{
    border: none !important;
}
nav.nav-justified
{
    font-size: 17px;
    font-weight: 700;

    margin-top: 96px;
}
nav.nav-justified li
{
    padding-bottom: 36px;
}
nav.nav-justified li.active
{
    background-image: url(../media/UI_tab-pointer.svg);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 55px 28px;
}
nav.notjustified li
{
    padding-bottom: 12px;
}
nav.notjustified li.active
{
    background-image: url(../media/UI_tab-pointer.svg);
    background-repeat: no-repeat;
    background-position: center bottom;
    background-size: 28px 14px;
}
nav a
{
    color: #0096c7 !important;
    border: none !important;
}
.logo
{
    margin-top: 24px;
    padding-left: 30px;
}
.modal-header
{
    color: white;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
}
.modal-header a
{
    color: white;
}
.modal-content .listHostages
{
    margin-bottom: 24px;
}
#about div.row {
    margin-top: 30px;
}
#about h2 {
    margin-top: 0px;
    line-height: 1em;
    letter-spacing: -0.025em;
    font-size: 3em;
}
div.about hr {
    border-color: #91989c;
}
@media screen and (min-width: 992px)
{
    div.about.left {
        padding-left: 90px;
    }
}
div.about.left li {
    margin-bottom: 1em;
}
div.about.right p,div.about.right ul {
    font-size: 1.3em;
    line-height: 1.5em;
    margin-bottom: 1.5em;
    color: #232a2c;
    padding-right: 40px;
}
div.about.right h3 {
    margin-bottom: 1em;
}
div.about.right h3::before {
    content: "—";
    position: absolute;
    left: -15px;
}
div.about a {
    color: #0096C7;
}

